import "./style.css";
import headN from './head-n.png';
import headS from './head-s.png';
import headE from './head-e.png';
import headO from './head-o.png';
import appleP from './apple.png';

let headNImg = new Image();
headNImg.src = headN;
let headSImg = new Image();
headSImg.src = headS;
let headEImg = new Image();
headEImg.src = headE;
let headOImg = new Image();
headOImg.src = headO;
let appleImg = new Image();
appleImg.src = appleP;


const canvas = document.querySelector("canvas");
const ctx = canvas.getContext("2d");
let snake = [[9, 9], [8, 9], [7, 9]];
let gridElem = 40;
let apple = [5, 5];
let direction = 'e';
let speed = 600;
let score = 0;
let head;
let pad = document.getElementById('pad')
window.onload = () => {
    const getDeviceType = () => {
        const ua = navigator.userAgent;
        if (/(tablet|ipad|playbook|silk)|(android(?!.*mobi))/i.test(ua)) {
            pad.style.display = 'flex'
            canvas.width = window.screen.width / 100 * 80;
            canvas.height = window.screen.width / 100 * 80;
            gridElem = canvas.width / 20
            return "tablet";
        }
        if (
            /Mobile|iP(hone|od)|Android|BlackBerry|IEMobile|Kindle|Silk-Accelerated|(hpw|web)OS|Opera M(obi|ini)/.test(
                ua
            )
        ) {
            canvas.width = window.screen.width / 100 * 95;
            canvas.height = window.screen.width / 100 * 95;
            gridElem = canvas.width / 20
            pad.style.display = 'flex'
            return "mobile";
        }
        canvas.width = 800;
        canvas.height = 800;
        gridElem = canvas.width / 20
        pad.style.display = 'none'
        return "desktop";
    };
    getDeviceType()
}

pad.addEventListener('click', (event) => {
    switch (event.target.id){
        case 'up':{
            direction = 'n'
            break
        }
        case 'upIcon': {
            direction = 'n'
            break
        }
        case 'down':{
            direction = 's'
            break
        }
        case 'downIcon':{
            direction = 's'
            break
        }
        case 'right':{
            direction = 'e'
            break
        }
        case 'rightIcon':{
            direction = 'e'
            break
        }
        case 'left':{
            direction = 'o'
            break
        }
        case 'leftIcon':{
            direction = 'o'
            break
        }
    }
})
